.container {
  width: 500px;
  height: 500px;
  border-radius: 5px;
  border: 1px solid #ededed;
  margin: 0 auto;
}
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}
.root {
  height: 100%;
}
/*# sourceMappingURL=src/assets/style/style.css.map */
#navigator{
  width: 200px;
  height: 176px;
  position: absolute;
  bottom: 0px;
  right: 0px;
  z-index: 3;
}
#navigator .slider{
  margin: 7px 10px 10px;
  float: left;
  width: 120px;
}
#navigator #minimap{
  width: 200px;
  height: 120px;
}
.zoom-dropdown-btn {
  color: rgba(0,0,0,0.45);
  margin-left: 4px;
  line-height: 24px;
  font-weight: 100;
  text-decoration : none;
}
.toolbar{
  padding: 8px 0px;
  width: 100%;
  border: 1px solid #E9E9E9;
  height: 42px;
  z-index: 3;
  box-shadow: 0px 8px 12px 0px rgba(0, 52, 107, 0.04);
  position: absolute;
}
.toolbar *::before{
  font-size: 16px !important;
}
.toolbar .disable{
  color: rgba(0,0,0,0.25);
}
.toolbar .icon-select.disable{
  background: #EEEEEE;
}
.toolbar .separator{
  margin: 4px;
  border-left: 1px solid #E9E9E9;
}
.toolbar .command{
  width: 27px;
  height: 27px;
  margin: 0px 6px;
  border-radius: 2px;
  padding-left: 4px;
  display: inline-block;
  border: 1px solid rgba(2,2,2,0);
}
.toolbar .command:nth-of-type(1){
  margin-left: 24px;
}
.toolbar .command:hover{
  cursor: pointer;
  border: 1px solid #E9E9E9;
}
.toolbar .disable:hover{
  cursor: default;
  border: 1px solid rgba(2,2,2,0);
}

.contextmenu{
  margin: 0px;
  width: 200px;
  background: white;
  box-shadow: 0px 1px 4px rgba(0,0,0,0.25);
  color: #000;
  font-size: 12px;
  display: none;
}
.contextmenu .disable{
  color: rgba(0,0,0,0.25);
}
.contextmenu .command{
  height: 12px;
  padding: 8px;
  box-sizing: content-box; 
}
.contextmenu .command:hover{
  cursor: pointer;
  background: #E6F7FF;
}
.contextmenu .command.disable:hover{
  cursor: default;
  background: none;
}
.contextmenu span:nth-of-type(1){
  float: left;
}
.contextmenu span:nth-of-type(2){
  float: right;
}
.itempannel{
  height: 100%;
  position: absolute;
  left: 0px;
  z-index: 2;
  background: #F7F9FB;
  width: 200px;
  padding-top: 8px;
  border-right: 1px solid #E6E9ED;
}
.itempannel img{
  width: 92px;
  height: 96px;
  padding: 4px;
  margin-left: 4px;
  border-radius: 2px;
  border: 1px solid rgba(0,0,0,0);
}
.itempannel img:hover{
  cursor: move;
  border: 1px solid #ccc;
}
.detail-pannel-span{
	display: block;
}
